import React, {useEffect} from "react";
import styled from "styled-components";
import HtmlIcon from "../assets/img/html.png";
import Css from "../assets/img/css.png";
import Js from "../assets/img/Javascript.png";
import Angular from "../assets/img/Angular.png";
import AOS from "aos";
import ReactLogo from "../assets/img/Imagen3.png";
import Mysql from "../assets/img/Imagen6.png";
import PhpLogo from "../assets/img/imagen7.png";
import LaravelLogo from "../assets/img/Imagen5.png";

const Services = () => {

    useEffect(() => {
    AOS.init({ duration: 1100 });
  }, []);
  return (
    <Contenedor>
     <div data-aos="fade-down"  >
        <H3  className="text-center">About Me</H3>
        <p  className="text-services">My name is Joaquin Varela, I am from Buenos Aires, Argentina. I am good at learning new things
        such as technologies or programming languages. 
        I go to a technical school where I study IT. I have been in the web development area for 2 years now.    
        </p>
      </div> 
     <H3 className="text-center">Tecnologies</H3>
      <ContServ data-aos="zoom-in">
        <img src={HtmlIcon} alt="Icono Html" className="html-width-increase"/>
        <img src={Css} alt="Icono Css"/>
        <img src={Js} alt="Icono Javascript"/>
        <img src={Angular} alt="Icono Angular" className="width-increase"/>
        <img src={ReactLogo} alt="Icono react" className="width-increase"/>
        <img src={Mysql} className="mql-grid " alt="Icono Mysql"  />
        <img src={PhpLogo} alt="Icono php" className="width-increase" />
        <img src={LaravelLogo} alt="Icono Laravel" className="width-increase" />
      </ContServ>

    </Contenedor>

  );
};

export default Services;

const Contenedor = styled.div`
  margin: 3rem auto 0 auto;
  background: rgba(8,63,158,1);
  background: -moz-linear-gradient(top, rgba(8,63,158,1) 0%, rgba(0,139,189,1) 82%, rgba(0,139,189,1) 100%);
  background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(8,63,158,1)), color-stop(82%, rgba(0,139,189,1)), color-stop(100%, rgba(0,139,189,1)));
  background: -webkit-linear-gradient(top, rgba(8,63,158,1) 0%, rgba(0,139,189,1) 82%, rgba(0,139,189,1) 100%);
  background: -o-linear-gradient(top, rgba(8,63,158,1) 0%, rgba(0,139,189,1) 82%, rgba(0,139,189,1) 100%);
  background: -ms-linear-gradient(top, rgba(8,63,158,1) 0%, rgba(0,139,189,1) 82%, rgba(0,139,189,1) 100%);
  background: linear-gradient(to bottom, rgba(8,63,158,1) 0%, rgba(0,139,189,1) 82%, rgba(0,139,189,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#083f9e', endColorstr='#008bbd', GradientType=0 );
  position: relative;
  height: 50vh;
  @media(max-width: 340px){
    padding-bottom: 23rem;
  }  
  
    padding: 03rem 0 20rem 0;
  
    `;
const ContServ = styled.div`
  margin: 0 auto;
  display: grid; 
  grid-template-columns: repeat(5, 1fr);
  align-items: center;
  box-align: center;
  @media (min-width: 500px) and (max-width: 768px){
    padding-bottom: 30vh;
    grid-template-columns: repeat(3, 1fr);
  }

  top: 50;
  background: white;
  width: 90%;
  border-radius: .5rem;
  box-shadow: 0px 2px 3px #6b6b6b; 
  img{
    max-width: 100px;
    margin: 0 auto; 
    padding: 1rem 0;
    transition: transform .5s ease-in-out;
   }
  img:hover{
  
  -webkit-transform: rotate(360deg);
  transform: rotate(360deg)  ;
  }


  @media(max-width: 500px){
    grid-template-columns: repeat(2, 1fr);
    img{
      max-width: 70px;
    }
  }
`;

const H3 = styled.h3`
  color: white;
  font-size: 1.6rem; 
`;